.cart-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem 120px; /* Extra padding at bottom for navbar */
}

.cart-container h1 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 2rem;
  text-align: center;
}

.cart-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 1.5rem;
}

.cart-items-section {
  width: 100%;
}

.cart-header {
  display: grid;
  grid-template-columns: 3fr 1fr 1.5fr 1fr 1fr;
  background-color: #f8f9fa;
  padding: 1rem;
  border-radius: 10px 10px 0 0;
  font-weight: 700;
  color: #555;
  text-align: center;
  align-items: center;
}

.cart-header-product {
  text-align: left;
  padding-left: 1rem;
}

.cart-items {
  border: 1px solid #eee;
  border-radius: 0 0 10px 10px;
  overflow: hidden;
  margin-bottom: 2rem;
}

.cart-item {
  display: grid;
  grid-template-columns: 3fr 1fr 1.5fr 1fr 1fr;
  padding: 1.2rem 1rem;
  border-bottom: 1px solid #eee;
  align-items: center;
  background-color: white;
  text-align: center;
}

.cart-item:last-child {
  border-bottom: none;
}

.cart-item:hover {
  background-color: #f9f9f9;
}

.cart-item-product {
  display: flex;
  align-items: center;
  text-align: left;
}

.cart-item-product img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 1rem;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.cart-item-details h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.1rem;
  color: #333;
}

.cart-item-category {
  font-size: 0.8rem;
  color: #666;
  background-color: #f0f0f0;
  padding: 0.2rem 0.5rem;
  border-radius: 20px;
}

.cart-item-price, .cart-item-total {
  font-weight: 600;
  color: #333;
}

.cart-item-quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.quantity-btn {
  background-color: #f0f0f0;
  border: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s;
}

.quantity-btn .material-icons {
  font-size: 18px;
}

.quantity-btn:hover {
  background-color: #e0e0e0;
}

.quantity-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.cart-item-actions {
  text-align: center;
}

.remove-btn {
  background-color: transparent;
  color: #e74c3c;
  border: 1px solid #e74c3c;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-btn .material-icons {
  font-size: 18px;
}

.remove-btn:hover {
  background-color: #e74c3c;
  color: white;
}

/* Order summary section */
.order-summary {
  background-color: white;
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 20px;
  height: fit-content;
}

.order-summary h2 {
  font-size: 1.5rem;
  color: #333;
  margin-top: 0;
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #eee;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 0;
  border-bottom: 1px solid #f5f5f5;
}

.summary-row.total {
  border-top: 1px solid #eee;
  border-bottom: none;
  padding-top: 1rem;
  margin-top: 0.5rem;
  font-weight: 700;
  font-size: 1.2rem;
}

.cart-actions {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  flex-wrap: wrap;
}

.clear-cart-btn {
  background-color: #f8f9fa;
  color: #666;
  border: 1px solid #ddd;
  padding: 0.75rem 1.25rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.clear-cart-btn:hover {
  background-color: #e9ecef;
}

.continue-shopping-btn {
  background-color: #f8f9fa;
  color: #3498db;
  border: 1px solid #3498db;
  padding: 0.75rem 1.25rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.continue-shopping-btn:hover {
  background-color: #eef5fd;
  box-shadow: 0 2px 5px rgba(52, 152, 219, 0.2);
}

.checkout-btn {
  background-color: #2ecc71;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.checkout-btn:hover {
  background-color: #27ae60;
}

.checkout-btn:disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
}

/* Secure checkout and payment methods */
.secure-checkout {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin: 1rem 0;
  color: #555;
  font-size: 0.9rem;
}

.payment-methods {
  margin-top: 1.5rem;
  text-align: center;
}

.payment-methods span {
  display: block;
  font-size: 0.9rem;
  color: #777;
  margin-bottom: 0.75rem;
}

.payment-icons {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
}

.payment-icon {
  display: inline-block;
  padding: 0.5rem 0.75rem;
  background-color: #f8f9fa;
  border-radius: 5px;
  font-style: normal;
  font-weight: 600;
  font-size: 0.8rem;
}

.payment-icon.visa {
  color: #1a1f71;
}

.payment-icon.mastercard {
  color: #eb001b;
}

.payment-icon.amex {
  color: #006fcf;
}

.payment-icon.paypal {
  color: #003087;
}

/* Notification */
.cart-notification {
  position: fixed;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #2ecc71;
  color: white;
  padding: 1rem 2rem;
  border-radius: 30px;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  animation: slide-up 0.3s ease-out forwards;
  font-size: 1.1rem;
  font-weight: 500;
  max-width: 80%;
  text-align: center;
}

@keyframes slide-up {
  from {
    opacity: 0;
    transform: translate(-50%, 20px);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

/* Empty cart styles */
.empty-cart {
  text-align: center;
}

.empty-cart-message {
  background-color: white;
  border-radius: 10px;
  padding: 3rem 1rem;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.cart-icon {
  font-size: 4rem;
  color: #3498db;
}

.empty-cart-message p {
  font-size: 1.2rem;
  color: #555;
  margin: 0;
}

/* Empty cart styles */
.empty-cart .continue-shopping-btn {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 2px 8px rgba(52, 152, 219, 0.3);
  margin-top: 1rem;
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .cart-header, .cart-item {
    grid-template-columns: 2.5fr 1fr 1.5fr 1fr 1fr;
  }
}

@media (max-width: 768px) {
  .cart-content {
    grid-template-columns: 1fr;
  }
  
  .cart-header {
    display: none;
  }
  
  .cart-item {
    grid-template-columns: 1fr;
    gap: 1rem;
    text-align: left;
    padding: 1.5rem;
  }
  
  .cart-item-product {
    border-bottom: 1px solid #f5f5f5;
    padding-bottom: 1rem;
  }
  
  .cart-item-price, .cart-item-total, .cart-item-quantity, .cart-item-actions {
    display: flex;
    align-items: center;
  }
  
  .cart-item-price::before {
    content: "Price: ";
    font-weight: 600;
    margin-right: 0.5rem;
  }
  
  .cart-item-quantity::before {
    content: "Quantity: ";
    font-weight: 600;
    margin-right: 0.5rem;
  }
  
  .cart-item-total::before {
    content: "Total: ";
    font-weight: 600;
    margin-right: 0.5rem;
  }
  
  .cart-item-actions {
    justify-content: flex-end;
  }
  
  .cart-actions {
    justify-content: space-between;
  }
}

/* Order Confirmation Modal */
.order-confirmation-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.order-confirmation-modal {
  background-color: white;
  border-radius: 10px;
  padding: 2.5rem;
  width: 90%;
  max-width: 500px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  animation: modal-appear 0.3s ease-out forwards;
}

@keyframes modal-appear {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.order-success-icon {
  margin-bottom: 1.5rem;
}

.order-success-icon .material-icons {
  font-size: 5rem;
  color: #2ecc71;
}

.order-confirmation-modal h2 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1rem;
}

.order-confirmation-modal p {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 0.5rem;
}

.continue-btn {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 0.9rem 2rem;
  border-radius: 30px;
  font-size: 1.1rem;
  font-weight: 600;
  margin-top: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(52, 152, 219, 0.3);
}

.continue-btn:hover {
  background-color: #2980b9;
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(52, 152, 219, 0.4);
} 