.search-page {
  padding: 1rem;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 100px; /* Space for bottom navbar */
  background-color: #1a1a1a;
  min-height: 100vh;
  color: #ffffff;
}

.search-header {
  text-align: center;
  margin-bottom: 2rem;
  padding: 2rem;
  background: linear-gradient(135deg, #6b21a8, #f927be);
  color: white;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.search-header h1 {
  font-size: 2.2rem;
  margin-bottom: 0.5rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.search-container {
  background-color: #121212;
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(150, 150, 150, 0.2);
}

.search-form {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.search-input {
  flex: 1;
  padding: 0.75rem 1rem;
  background-color: #2d2d2d;
  border: 2px solid rgba(107, 33, 168, 0.3);
  color: #ffffff;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.search-input:focus {
  border-color: #f927be;
  outline: none;
  box-shadow: 0 0 10px rgba(249, 39, 190, 0.3);
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.search-button {
  background-color: #6b21a8;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 8px;
}

.search-button:hover {
  background-color: #f927be;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(249, 39, 190, 0.4);
}

.search-button:disabled {
  background-color: #444444;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.search-button .material-icons {
  font-size: 20px;
}

/* Search results container - grid layout */
.search-results-container {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 2rem;
}

.search-results {
  width: 100%;
}

.search-results-info {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(150, 150, 150, 0.2);
}

.search-results-info h2 {
  color: #ffffff;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.search-results-info p {
  color: #e0e0e0;
  font-style: italic;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
}

/* Price range filter */
.price-range-filter {
  padding: 10px;
}

.price-inputs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.price-input {
  display: flex;
  align-items: center;
}

.price-input label {
  margin-right: 8px;
  color: #e0e0e0;
  font-size: 0.9rem;
}

.price-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  background-color: #2d2d2d;
  border: 1px solid rgba(150, 150, 150, 0.2);
  border-radius: 4px;
  padding: 0 0 0 8px;
}

.price-wrapper .currency-symbol {
  color: #e0e0e0;
  font-weight: 500;
}

.price-input input {
  width: 70px;
  background-color: transparent;
  border: none;
  color: #ffffff;
  padding: 6px 8px;
  font-size: 0.9rem;
}

.price-input input:focus {
  outline: none;
}

.price-range-slider {
  position: relative;
  height: 30px;
  margin: 10px 0 20px;
}

.price-slider {
  position: absolute;
  width: 100%;
  height: 4px;
  background: #2d2d2d;
  -webkit-appearance: none;
  pointer-events: none;
}

.price-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  background: #6b21a8;
  border-radius: 50%;
  pointer-events: auto;
  cursor: pointer;
  transition: all 0.2s ease;
}

.price-slider::-webkit-slider-thumb:hover,
.price-slider:active::-webkit-slider-thumb {
  background: #f927be;
  box-shadow: 0 0 8px rgba(249, 39, 190, 0.6);
}

.price-slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  background: #6b21a8;
  border: none;
  border-radius: 50%;
  pointer-events: auto;
  cursor: pointer;
  transition: all 0.2s ease;
}

.price-slider::-moz-range-thumb:hover,
.price-slider:active::-moz-range-thumb {
  background: #f927be;
  box-shadow: 0 0 8px rgba(249, 39, 190, 0.6);
}

.min-slider {
  z-index: 2;
}

.max-slider {
  z-index: 1;
}

/* Loading, Error, No Results States */
.search-loading, 
.search-error, 
.search-prompt,
.no-results {
  text-align: center;
  padding: 3rem 1rem;
  color: #e0e0e0;
}

/* Vinyl Record Loader */
.vinyl-loader {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.vinyl-record {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #000000;
  border: 2px solid rgba(249, 39, 190, 0.7);
  position: relative;
  animation: spin 2s linear infinite;
}

.vinyl-mini-loader .vinyl-record {
  width: 60px;
  height: 60px;
}

.vinyl-label {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #ffffff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid #000000;
}

.vinyl-mini-loader .vinyl-label {
  width: 24px;
  height: 24px;
}

/* Infinity scroll loader */
.infinite-scroll-loader {
  margin: 2rem 0;
  text-align: center;
  padding: 1rem;
  border-radius: 8px;
}

.scroll-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(107, 33, 168, 0.1);
  padding: 1rem;
  border-radius: 8px;
  border-left: 4px solid #f927be;
}

.scroll-loading p {
  margin-top: 0.5rem;
  color: #e0e0e0;
  font-size: 0.9rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Notification */
.notification {
  position: fixed;
  bottom: -100px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #121212;
  color: #ffffff;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  opacity: 0;
  transition: all 0.3s ease;
  border-left: 4px solid #f927be;
}

.notification.show {
  bottom: 30px;
  opacity: 1;
}

.notification .material-icons {
  color: #f927be;
}

.notification p {
  margin: 0;
}

/* Hide loader element when not loading */
.infinite-scroll-loader:empty {
  display: none;
}

.loader {
  border: 4px solid #2d2d2d;
  border-top: 4px solid #f927be;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

.search-error {
  color: #f44336;
}

.search-error button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  margin-top: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.search-error button:hover {
  background-color: #d32f2f;
  box-shadow: 0 2px 8px rgba(244, 67, 54, 0.4);
}

.no-results p {
  margin-bottom: 0.5rem;
}

.no-results .clear-all-btn {
  margin-top: 1rem;
  background-color: transparent;
  color: #f927be;
  border: 1px solid #f927be;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.no-results .clear-all-btn:hover {
  background-color: #f927be;
  color: #ffffff;
  box-shadow: 0 0 12px rgba(249, 39, 190, 0.5);
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .search-results-container {
    grid-template-columns: 1fr;
  }
  
  .filters-sidebar {
    margin-bottom: 2rem;
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .search-form {
    flex-direction: column;
  }
  
  .search-input, 
  .search-button {
    width: 100%;
  }
  
  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1rem;
  }
  
  .search-header {
    padding: 1.5rem;
  }
  
  .search-header h1 {
    font-size: 1.75rem;
  }
} 